nav{
    background: rgba(100, 53, 14, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(10px);
}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--light-color);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(102, 49, 0, 0.3);
}

nav a.active{
    background: var(--primary-color);
    color: var(--background-color);
}