.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 3rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--primary-color),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(-10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0deg);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  background: var(--background-color-lighter);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem 0.5rem;
  text-align: center;
  transition: var(--transition);
}

.about__card:hover {
  background: transparent;
  border-color: var(--primary-color);
  cursor: default;
}

.about__icon {
  color: var(--primary-color);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__card > h5 {
  font-size: 0.95rem;
}

.about__card > small {
  font-size: 0.7rem;
  color: var(--light-color);
}

.about__content > p {
  margin: 2rem 0 2.6rem;
  color: var(--light-color);
  text-align: center;
}

/* MEDIA QUERIES */

/* ========= Medium Devices - Tablets ==========  */
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content > p {
    margin: 1rem 0 1.5rem;
  }
}

/* ========= Small Devices - Mobiles ==========  */
@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .about__card:last-child {
    grid-column: 1 / span 2;
  }

  .about__content {
    text-align: center;
  }

  .about__content > p {
    margin: 1.5rem;
  }
}
