header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ========== CTA ========== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

/* ================ HEADER SOCIALS =============  */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--primary-color);
}

/* ============ HERO IMAGE ========== */
.me {
  background: linear-gradient(var(--primary-color), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  /* can also combine the above two lines and replace with this -> left: calc(50% - 11rem);*/
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1rem 1.5rem 1.5rem 1.5rem;
}

/* ============= SIDE SCROLL DOWN =========== */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 200;
  font-size: 0.9rem;
}

/* MEDIA QUERIES */

/* ========= Medium Devices - Tablets ==========  */
@media screen and (max-width: 1024px) {
  header {
    height: 70vh;
  }
}

/* ========= Small Devices - Mobiles ==========  */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .me {
    width: 70vw;
    height: auto;
  }
}
